<template>
  <div id="app">
    <view-box>
        <!--  -->
        <x-header
          class="header"
          slot="header"
          :left-options="{showBack:true}"
        >
          <div slot="left"></div>  <!--  -->
          <div></div>  <!--  -->
          <div slot="right"></div>  <!--  -->
        </x-header>

        <!--  -->
        <!-- Scroller as scdiv -->
        <sc
          :lock-y="true"
        >
          <div class="tab">
            <tab>
              <tab-item selected></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
              <tab-item></tab-item>
            </tab>
          </div>
        </sc>

        <scroller
          class="my-scroller"
          :on-refresh="refresh"
          :on-infinite="infinite"
          ref="myRef"
        >
          <!--  -->
          <swiper
            :list="swiperlist"
            v-model="swiperIndex"
            :loop="true"
            :auto="true"
            :interval=1000
          >
          </swiper>

          <!--  -->
          <marquee
            class="my-marquee"
          >
            <marquee-item v-for="(list,index) in marrqueeList">{{list.title}}</marquee-item>
          </marquee>

          <!--  -->
          <panel
            :list="dataList"
          ></panel>

          <panel
            :list="moreDataList"
          ></panel>

        </scroller>


        <!--  -->
        <tabbar slot='bottom'>
          <tabbar-item>
            <img src="./assets/icon_nav_article.png" alt="" slot="icon">
            <span slot="label"></span>
          </tabbar-item>
          <tabbar-item>
            <img src="./assets/icon_nav_cell.png" alt="" slot="icon">
            <span slot="label"></span>
          </tabbar-item>
          <tabbar-item>
            <img src="./assets/icon_nav_msg.png" alt="" slot="icon">
            <span slot="label"></span>
          </tabbar-item>
          <tabbar-item>
            <img src="./assets/icon_nav_button.png" alt="" slot="icon">
            <span slot="label"></span>
          </tabbar-item>
        </tabbar>
    </view-box>

    <router-view></router-view>
  </div>
</template>

<script>
import { ViewBox, XHeader, Tabbar, TabbarItem, Tab, TabItem, Scroller as sc, Swiper,Marquee, MarqueeItem, Panel } from 'vux'

var refreshKey = ['A', 'B01', 'B02', 'B03', 'B04', 'B05', 'B06', 'B07', 'B08', 'B09', 'B010'];
var key = 0;
var start = 0;
var end = start + 9; //

var keyValue = 'A';

var initLoaded = false;//

function getRefreshKey(){
  key++;
  if(key === refreshKey.length-1){
    key = 0;
  }
  keyValue = refreshKey[key];
}

export default {
  name: 'app',
  components: {
    ViewBox,
    XHeader,
    Tabbar,
    TabbarItem,
    Tab,
    TabItem,
    sc,
    Swiper,
    Marquee,
    MarqueeItem,
    Panel
  },// vue-scrollerscrollerScrollersc
  created(){  //
    // http://3g.163.com/touch/jsonp/sy/recommend/0-9.htmlthen()
    this.$jsonp('http://3g.163.com/touch/jsonp/sy/recommend/0-9.html').then(data=>{

      console.log(data);

      // 1
      // datafocus,swiperlist
      // map() ,
      this.swiperlist = data.focus.filter(item=>{
        return item.addata === null && item.picInfo[0];
      }).map(item=>{
        return{
          url: item.link,
          img: item.picInfo[0].url,
          title: item.title
        }
      })

      // 2
      this.marrqueeList = data.live.filter(item=>{
        return item.addata === null && item.picInfo[0];
      }).map(item=>{
        return{
          title: item.title
        }
      })

      // 3
      this.dataList = data.list.filter(item=>{
        return item.addata === null && item.picInfo[0];
      }).map(item=>{
        return{
          src: item.picInfo[0].url,
          title: item.title,
          desc: item.digest,
          url: 'item.link'
        }
      })

      initLoaded = true;

    })
  },
  data(){
    // var dataList=[];
    // for(var i=0;i<10;i++){
    //   dataList.push({
    //     src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
    //     title: '',
    //     desc: '',
    //     url: '/component/cell'
    //   })
    // }
    return{
      swiperlist: [],  //
      swiperIndex: 0,  //
      marrqueeList: [],
      dataList: [],
      moreDataList: []
    }
  },
  methods: {
    refresh(){
      // console.log(11);
      // 
      getRefreshKey()
      this.$jsonp('http://3g.163.com/touch/jsonp/sy/recommend/0-9.html',{
        miss: '00',
        refresh: keyValue
      }).then(data=>{
        console.log(data);
        // console.log(this.$refs.myRef);

        // 
        this.dataList = data.list.filter(item=>{
          return item.addata === null && item.picInfo[0];
        }).map(item=>{
          return{
            src: item.picInfo[0].url,
            title: item.title,
            desc: item.digest,
            url: 'item.link'
          }
        })
        this.$refs.myRef.finishPullToRefresh();
        this.$vux.toast.text(`${this.dataList.length}`, 'top');//https://vux.li/#/zh-CN/components?id=toast
      })
    },
    infinite(){
      if(!initLoaded){
        this.$refs.myRef.finishInfinite();
        return;
      }

      console.log(22);

      this.$jsonp('http://3g.163.com/touch/jsonp/sy/recommend/${start}-${end}.html',{
        miss: '00',
        refresh: keyValue
      }).then(data=>{

        // 
        setTimeout(()=>{
          var dataList = data.list.filter(item=>{
            return item.addata === null && item.picInfo[0];
          }).map(item=>{
            return{
              src: item.picInfo[0].url,
              title: item.title,
              desc: item.digest,
              url: 'item.link'
            }
          })

          this.moreDataList = this.moreDataList.concat(dataList)

          start += 10;
          end = start + 9;
          this.$refs.myRef.finishInfinite();
        },1000)
      })
    }
  }
}
</script>

<style lang='less'>
  @import '~vux/src/styles/reset.less';
  html,body{
    margin: 0;
    width:100%;
    height: 100%;
    overflow: hidden;
  }
  #app{
    height: 100%;

    .header{
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      z-index: 999;
    }
    .vux-header-back, .left-arrow{
      display: none;
    }
    .tab{
      margin-top: 46px;
      width: 1200px;
    }
    .my-scroller{
      top: 90px; /**/
    }
    .loading-layer{
      padding-bottom: 90px;
    }
    .my-marquee{
      margin: 10px 0 10px 0;
    }
    .weui-media-box__hd, .weui-media-box__hd img {
      width: 120px;
      height: 78px;
    }
    .weui-media-box__bd{
      height: 78px;
    }
    .weui-media-box__desc{
      font-size: 100%;
      font-family: ''
    }
  }
</style>



